@use './functions' as *;
@use './constants' as c;

@mixin centered-container {
  display: flex;
  justify-content: center;
}

@mixin vertical-centered-container {
  @include centered-container;
  flex-direction: column;
  align-items: center;
}

@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

@mixin box-shadow {
  // box-shadow: 0 5px 7px 0px rgba($color: #000, $alpha: 0.5);
  box-shadow: 0 8px 5px rgba(0, 0, 0, 0.1);
}

@mixin contained {
  background-color: rgba($color: white, $alpha: 0.6);
  padding: get-unit(2);
  border-radius: c.$border-radius;
}

@mixin reset-list {
  margin: 0;
  padding: 0;
  text-indent: 0;
  list-style-type: none;
}

// Extra Small devices
@mixin xs {
  @media (max-width: #{c.$screen-xs-max}) {
    @content;
  }
}

// Small devices
@mixin sm {
  @media (max-width: #{c.$screen-sm-max}) {
    @content;
  }
}

// Medium devices
@mixin md {
  @media (max-width: #{c.$screen-md-max}) {
    @content;
  }
}

// Large devices
@mixin lg {
  @media (max-width: #{c.$screen-lg-max}) {
    @content;
  }
}

// Mobile devices
@mixin mobile {
  @media (hover: none) and (pointer: coarse) {
    @content;
  }
}
